import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faFacebook,
    faTwitter,
    faInstagram
  } from "@fortawesome/free-brands-svg-icons";

export default function SocialFollow() {
  return (
    <div class="social-container">
    <a href="https://twitter.com/KSathvi" className="twitter social">
        <FontAwesomeIcon icon={faTwitter} size="3x" />
        </a>
    <a href="https://www.instagram.com/sathvik_korrapati"className="instagram social">
        <FontAwesomeIcon icon={faInstagram} size="3x" />
    </a>
    <a href="https://www.facebook.com/saisathvik.k.1"className="facebook social">
        <FontAwesomeIcon icon={faFacebook} size="3x" />
    </a>
    </div>
  );
}